        "Die von uns verarbeiteten Daten werden nach Magabe der Art. 17 und 18 DSGVO gelscht oder in ihrer Verarbeitung eingeschrnkt. Sofern nicht im Rahmen dieser Datenschutzerklrung ausdrcklich angegeben, werden die bei uns gespeicherten Daten gelscht, sobald sie fr ihre Zweckbestimmung nicht mehr erforderlich sind und der Lschung keine gesetzlichen Aufbewahrungspflichten entgegenstehen. Sofern die Daten nicht gelscht werden, weil sie fr andere und gesetzlich zulssige Zwecke erforderlich sind, wird deren Verarbeitung eingeschrnkt. D.h. die Daten werden gesperrt und nicht fr andere Zwecke verarbeitet. Das gilt z.B. fr Daten, die aus handels- oder steuerrechtlichen Grnden aufbewahrt werden mssen. Nach gesetzlichen Vorgaben in Deutschland, erfolgt die Aufbewahrung insbesondere fr 10 Jahre gem  147 Abs. 1 AO, 257 Abs. 1 Nr. 1 und 4, Abs. 4 HGB (Bcher, Aufzeichnungen, Lageberichte, Buchungsbelege, Handelsbcher, fr Besteuerung relevanter Unterlagen, etc.) und 6 Jahre gem  257 Abs. 1 Nr. 2 und 3, Abs. 4 HGB (Handelsbriefe). Nach gesetzlichen Vorgaben in sterreich erfolgt die Aufbewahrung insbesondere fr 7 J gem  132 Abs. 1 BAO (Buchhaltungsunterlagen, Belege/Rechnungen, Konten, Belege, Geschftspapiere, Aufstellung der Einnahmen und Ausgaben, etc.), fr 22 Jahre im Zusammenhang mit Grundstcken und fr 10 Jahre bei Unterlagen im Zusammenhang mit elektronisch erbrachten Leistungen, Telekommunikations-, Rundfunk- und Fernsehleistungen, die an Nichtunternehmer in EU-Mitgliedstaaten erbracht werden und fr die der Mini-One-Stop-Shop (MOSS) in Anspruch genommen wird.",

        "Erbringung unserer satzungs- und geschftsgemen Leistungen",
        "Wir verarbeiten die Daten unserer Mitglieder, Untersttzer, Interessenten, Kunden oder sonstiger Personen entsprechend Art. 6 Abs. 1 lit. b. DSGVO, sofern wir ihnen gegenber vertragliche Leistungen anbieten oder im Rahmen bestehender geschftlicher Beziehung, z.B. gegenber Mitgliedern, ttig werden oder selbst Empfnger von Leistungen und Zuwendungen sind. Im brigen verarbeiten wir die Daten betroffener Personen gem. Art. 6 Abs. 1 lit. f. DSGVO auf Grundlage unserer berechtigten Interessen, z.B. wenn es sich um administrative Aufgaben oder ffentlichkeitsarbeit handelt. Die hierbei verarbeiteten Daten, die Art, der Umfang und der Zweck und die Erforderlichkeit ihrer Verarbeitung bestimmen sich nach dem zugrundeliegenden Vertragsverhltnis. Dazu gehren grundstzlich Bestands- und Stammdaten der Personen (z.B., Name, Adresse, etc.), als auch die Kontaktdaten (z.B., E-Mailadresse, Telefon, etc.), die Vertragsdaten (z.B., in Anspruch genommene Leistungen, mitgeteilte Inhalte und Informationen, Namen von Kontaktpersonen) und sofern wir zahlungspflichtige Leistungen oder Produkte anbieten, Zahlungsdaten (z.B., Bankverbindung, Zahlungshistorie, etc.). Wir lschen Daten, die zur Erbringung unserer satzungs- und geschftsmigen Zwecke nicht mehr erforderlich sind. Dies bestimmt sich entsprechend der jeweiligen Aufgaben und vertraglichen Beziehungen. Im Fall geschftlicher Verarbeitung bewahren wir die Daten so lange auf, wie sie zur Geschftsabwicklung, als auch im Hinblick auf etwaige Gewhrleistungs- oder Haftungspflichten relevant sein knnen. Die Erforderlichkeit der Aufbewahrung der Daten wird alle drei Jahre berprft; im brigen gelten die gesetzlichen Aufbewahrungspflichten. ",

        "Hosting",
        "Die von uns in Anspruch genommenen Hosting-Leistungen dienen der Zurverfgungstellung der folgenden Leistungen: Infrastruktur- und Plattformdienstleistungen, Rechenkapazitt, Speicherplatz und Datenbankdienste, Sicherheitsleistungen sowie technische Wartungsleistungen, die wir zum Zwecke des Betriebs dieses Onlineangebotes einsetzen. Hierbei verarbeiten wir, bzw. unser Hostinganbieter Bestandsdaten, Kontaktdaten, Inhaltsdaten, Vertragsdaten, Nutzungsdaten, Meta- und Kommunikationsdaten von Kunden, Interessenten und Besuchern dieses Onlineangebotes auf Grundlage unserer berechtigten Interessen an einer effizienten und sicheren Zurverfgungstellung dieses Onlineangebotes gem. Art. 6 Abs. 1 lit. f DSGVO i.V.m. Art. 28 DSGVO (Abschluss Auftragsverarbeitungsvertrag).",

        "Erhebung von Zugriffsdaten und Logfiles",
        "Wir, bzw. unser Hostinganbieter, erhebt auf Grundlage unserer berechtigten Interessen im Sinne des Art. 6 Abs. 1 lit. f. DSGVO Daten ber jeden Zugriff auf den Server, auf dem sich dieser Dienst befindet (sogenannte Serverlogfiles). Zu den Zugriffsdaten gehren Name der abgerufenen Webseite, Datei, Datum und Uhrzeit des Abrufs, bertragene Datenmenge, Meldung ber erfolgreichen Abruf, Browsertyp nebst Version, das Betriebssystem des Nutzers, Referrer URL (die zuvor besuchte Seite), IP-Adresse und der anfragende Provider. Logfile-Informationen werden aus Sicherheitsgrnden (z.B. zur Aufklrung von Missbrauchs- oder Betrugshandlungen) fr die Dauer von maximal 7 Tagen gespeichert und danach gelscht. Daten, deren weitere Aufbewahrung zu Beweiszwecken erforderlich ist, sind bis zur endgltigen Klrung des jeweiligen Vorfalls von der Lschung ausgenommen.",

        "Onlineprsenzen in sozialen Medien",
        "Wir unterhalten Onlineprsenzen innerhalb sozialer Netzwerke und Plattformen, um mit den dort aktiven Kunden, Interessenten und Nutzern kommunizieren und sie dort ber unsere Leistungen informieren zu knnen. Beim Aufruf der jeweiligen Netzwerke und Plattformen gelten die Geschftsbedingungen und die Datenverarbeitungsrichtlinien deren jeweiligen Betreiber. Soweit nicht anders im Rahmen unserer Datenschutzerklrung angegeben, verarbeiten wir die Daten der Nutzer sofern diese mit uns innerhalb der sozialen Netzwerke und Plattformen kommunizieren, z.B. Beitrge auf unseren Onlineprsenzen verfassen oder uns Nachrichten zusenden.",
        
