        "Vonatkoz jogalapok",
        "A GDPR 13. cikknek megfelelen tjkoztatjuk nt adatkezelsnk jogalapjrl. Ha a jogalap nem szerepel az adatvdelmi nyilatkozatban, az albbiak rvnyesek: A hozzjruls megszerzsnek jogalapja az Art. 6. bek. Az 1. cikk a) pontja s a GDPR 7. cikke szerint a szolgltatsaink teljestse s a szerzdses intzkedsek vgrehajtsa, valamint a megkeressek megvlaszolsa rdekben vgzett adatkezels jogalapja a GDPR 6. cikk (1) bekezds b) pontja, a jogalapjaink teljestse rdekben vgzett adatkezels jogalapja. ktelezettsgeit a GDPR 6. cikk (1) bekezds 6. (1) bekezds f) pontja, a jogos rdekeink vdelmt szolgl adatkezels jogalapja pedig a GDPR abban az esetben, ha az rintett vagy ms termszetes szemly ltfontossg rdekei megkvnjk a szemlyes adatok kezelst. . 6 A GDPR (1) bekezds d) pontja mint jogalap.",

        "Biztonsgi intzkedsek",
